
.portfolio h3 {
  color: black;
  text-align: left;  
  margin-left: 20px;
}

.portfolio-underline {
  height: 3px;
    background-color:   palevioletred;
    width: 55%;
    margin-top: 10px;
    margin-bottom: 20px;
    margin-left: 10px;
}

  .portfolio-container {
    margin-top: 50px;
    display: flex;
    flex-wrap: row nowrap;
    justify-content: space-between;
    text-align: center;
    margin-left: 20px;
    margin-right: 20px;
  }
  .portfolio-container img {
    width: 17vw;
    height: 17vw;
  }



  .portfolio-container img {
    width: 25vw;
  }


.portfolio-container img {
  border: 1px solid rgb(128, 128, 128);
}

.portfolio-box p {
  font-size: 15px;
  margin-bottom: 6px;
 
}

.portfolio-box h5 {
  color: palevioletred;
  margin-top: 10px;
}

.portfolio-box h5:hover {
  cursor: pointer;
  color: #735d78;
}

.portfolio-box h6 {
  font-size: 30px;
  font-weight: 1000;
  color: palevioletred;
  margin-top: -4px;
  margin-bottom: -14px;
  transition: transform 0.8s ease-in-out;
}

.portfolio-box h6:hover {
  cursor: pointer;
  transform: rotate(360deg);
}

