.code-list {
  display: flex;
  flex-direction: column;
}

.code-list p {
  font-size: 15px;
  font-weight: 400;
  margin: 0;
  margin-bottom: 2px;
}

.portfolio-box a {
  text-decoration: none;
  font-size: 13px;
  font-weight: 500;
  color: white;
  background-color: #735d78;
  width: 20%;
  margin: 0 auto;
  padding: 6px 4px 6px 4px;
  margin-top: 10px;
  margin-bottom: 5px;
}

.portfolio-box a:hover {
  background-color: palevioletred;
}



